/* global CustomFunctions, AbortController */
import { arborAxiosLimiter } from "../http/limiter";
import { ArborScreenFailResult, ArborScreenResult } from "./types";
import { AxiosError } from "axios";
import { returnErrorEntity } from "./utils";
import { cellProvider, successCellIcon } from "../constants";

/**
 * Screens stocks based on given criteria.
 * @customfunction ARBOR_SCREEN
 * @param {string} query The screening criteria
 * @param {CustomFunctions.CancelableInvocation} invocation Invocation object.
 * @returns {Promise<Excel.EntityCellValue>} An array of stock symbols that match the criteria
 * @cancellable
 */
export async function screen(
  query: string,
  invocation: CustomFunctions.CancelableInvocation
): Promise<Excel.EntityCellValue> {
  const controller = new AbortController();

  invocation.onCanceled = () => {
    controller.abort();
  };

  if (!query.trim()) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Query is required");
  }

  try {
    const response = await arborAxiosLimiter.post(
      `/api/v1/screener/stock-suggestions`,
      { query },
      { signal: controller.signal }
    );

    if (!response?.data?.result) {
      throw new AxiosError(`NoResult`, "NoResult", response.config);
    }

    const entity = {
      type: Excel.CellValueType.entity,
      text: `Screen: ${query}`,
      provider: cellProvider,
      properties: {
        success: {
          type: Excel.CellValueType.boolean,
          basicValue: true,
        },
        type: {
          type: Excel.CellValueType.string,
          basicValue: "ARBOR_SCREEN",
        },
        query: {
          type: Excel.CellValueType.string,
          basicValue: query,
        },
        answer: {
          type: Excel.CellValueType.array,
          elements: response.data.result.map((i) => [i.symbol, i.name]),
        },
        answeredAt: {
          type: Excel.CellValueType.string,
          basicValue: response.config["metadata"].end.toISOString(),
        },
        queriedAt: {
          type: Excel.CellValueType.string,
          basicValue: response.config["metadata"].start.toISOString(),
        },
        ...(response.data.references?.length > 0
          ? {
              references: {
                type: Excel.CellValueType.array,
                elements: response.data.references.map((ref) => [ref.url as string, ref.title as string]),
              },
            }
          : {}),
      },
      layouts: {
        compact: {
          icon: successCellIcon,
        },
        card: {
          title: {
            property: "query",
          },
          sections: [
            {
              layout: "List",
              properties: ["answer", ...(response.data.references?.length > 0 ? ["references"] : [])],
            },
            {
              title: "Metadata",
              layout: "Table",
              collapsible: true,
              collapsed: false,
              properties: ["queriedAt", "answeredAt", "type", "success"],
            },
          ],
        },
      },
    } as ArborScreenResult;
    console.log(entity);
    return entity;
  } catch (error: any | AxiosError) {
    return returnErrorEntity<ArborScreenFailResult>(error, {
      type: {
        type: Excel.CellValueType.string,
        basicValue: "ARBOR_SCREEN",
      },
      query: {
        type: Excel.CellValueType.string,
        basicValue: query || "",
      },
    });
  }
}

CustomFunctions.associate("ARBOR_SCREEN", screen);