/* global CustomFunctions, AbortController */

import { arborAxiosLimiter } from "../http/limiter";
import {
  ArborDeepFailResult,
  ArborDeepResult,
  ArborDeepSuccessResult,
  ArborFastSuccessResult,
  ArborNumberFailResult,
  ArborNumberSuccessResult,
} from "./types";
import { arborDeep } from "./arborDeep";
import { arborFast } from "./arborFast";
import { cellProvider, successCellIcon } from "../constants";
import { returnErrorEntity } from "./utils";
import { AxiosError } from "axios";
async function numberExtractRequest(ticker: string, query: string, context: string, controller: AbortController) {
  return arborAxiosLimiter.post(
    "/api/v1/screener/numeric-extract",
    {
      query: query,
      context: context,
      stockSymbol: ticker,
    },
    {
      signal: controller.signal,
    }
  );
}
/**
 * Answers questions about a company based on its stock symbol in a short format.
 * @customfunction ARBOR_NUMBER
 * @param {string} ticker The stock symbol of the company
 * @param {string} query The question to ask about the company
 * @param {CustomFunctions.CancelableInvocation} invocation Invocation object.
 * @returns {Promise<Excel.EntityCellValue | Excel.ErrorCellValue>} The answer to the query
 * @cancellable
 */
export async function answerNumber(
  ticker: string,
  query: string,
  invocation: CustomFunctions.CancelableInvocation
): Promise<Excel.EntityCellValue | Excel.ErrorCellValue> {
  if (!query.trim()) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Query is required");
  }
  if (!ticker.trim()) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Ticker is required");
  }
  let queryType: "ARBOR_FAST" | "ARBOR_DEEP" = "ARBOR_FAST";
  try {
    const fastAnswer = await arborFast(ticker, query, invocation).catch(() => null);
    if (fastAnswer && fastAnswer?.properties?.answer?.basicValue) {
      const context = fastAnswer.properties.answer.basicValue;
      const controller = new AbortController();
      invocation.onCanceled = () => {
        controller.abort();
      };
      const numberAnswerResponse = await numberExtractRequest(ticker, query, context, controller);
      if (!numberAnswerResponse.data?.result) {
        throw new AxiosError(
          "FailToAnswer",
          numberAnswerResponse.status.toString(),
          numberAnswerResponse.config,
          numberAnswerResponse.request
        );
      }
      const numberAnswer = numberAnswerResponse.data.result;
      const entity: ArborNumberSuccessResult = {
        type: Excel.CellValueType.entity,
        text: `${numberAnswer.answer}`,
        provider: cellProvider,
        properties: {
          ...(fastAnswer as ArborFastSuccessResult).properties,
          type: {
            type: Excel.CellValueType.string,
            basicValue: "ARBOR_NUMBER",
          },
          queryType: {
            type: Excel.CellValueType.string,
            basicValue: "ARBOR_FAST",
          },
          answer: {
            type: Excel.CellValueType.double,
            basicValue: numberAnswer.answer,
          },
          reason: {
            type: Excel.CellValueType.string,
            basicValue: numberAnswer.reason,
          },
        },
        layouts: {
          card: {
            title: {
              property: "query",
            },
            subTitle: {
              property: "ticker",
            },
            sections: [
              {
                layout: "List",
                properties: ["answer", "reason", ...(fastAnswer.properties.references > 0 ? ["references"] : [])],
              },
              {
                title: "Metadata",
                layout: "Table",
                collapsible: true,
                collapsed: false,
                properties: ["ticker", "queryType", "queriedAt", "answeredAt", "type", "success"],
              },
            ],
          },
        },
      };
      console.log(entity);
      return entity;
    }

    queryType = "ARBOR_DEEP";
    const deepResponse: ArborDeepResult = (await arborDeep(ticker, query, false, invocation)) as ArborDeepResult;
    if (!deepResponse.properties.success.basicValue) {
      const data = deepResponse as ArborDeepFailResult;
      return returnErrorEntity<ArborNumberFailResult>(new Error(data.properties.error.basicValue), {
        type: {
          type: Excel.CellValueType.string,
          basicValue: "ARBOR_NUMBER",
        },
        queryType: {
          type: Excel.CellValueType.string,
          basicValue: queryType,
        },
        query: {
          type: Excel.CellValueType.string,
          basicValue: query,
        },
        ticker: {
          type: Excel.CellValueType.string,
          basicValue: ticker,
        },
        ...(data.properties?.queriedAt?.basicValue && {
          queriedAt: {
            type: Excel.CellValueType.string,
            basicValue: data.properties.queriedAt?.basicValue || "",
          },
        }),
      });
    }

    const context = (deepResponse as ArborDeepSuccessResult).properties.answer.basicValue;
    const controller = new AbortController();
    invocation.onCanceled = () => {
      controller.abort();
    };
    const numberAnswerResponse = await numberExtractRequest(ticker, query, context, controller);
    if (!numberAnswerResponse.data?.result) {
      throw new AxiosError(
        "FailToAnswer",
        numberAnswerResponse.status.toString(),
        numberAnswerResponse.config,
        numberAnswerResponse.request
      );
    }

    const numberAnswer = numberAnswerResponse.data.result;
    const entity: ArborNumberSuccessResult = {
      type: Excel.CellValueType.entity,
      text: `${numberAnswer.answer}`,
      provider: deepResponse.provider,
      properties: {
        ...(deepResponse as ArborDeepSuccessResult).properties,
        type: {
          type: Excel.CellValueType.string,
          basicValue: "ARBOR_NUMBER",
        },
        success: {
          type: Excel.CellValueType.boolean,
          basicValue: true,
        },
        queryType: {
          type: Excel.CellValueType.string,
          basicValue: queryType,
        },
        answer: {
          type: Excel.CellValueType.double,
          basicValue: numberAnswer.answer,
        },
        reason: {
          type: Excel.CellValueType.string,
          basicValue: numberAnswer.reason,
        },
      },
      layouts: {
        compact: {
          icon: successCellIcon,
        },
        card: {
          title: {
            property: "query",
          },
          subTitle: {
            property: "ticker",
          },
          sections: [
            {
              layout: "List",
              properties: [
                "answer",
                "reason",
                "url",
                ...((deepResponse as ArborDeepSuccessResult).properties.references ? ["references"] : []),
              ],
            },
            {
              title: "Metadata",
              layout: "Table",
              collapsible: true,
              collapsed: false,
              properties: [
                "ticker",
                "queryType",
                "answerId",
                "isLongAnswer",
                "queriedAt",
                "answeredAt",
                "type",
                "success",
              ],
            },
          ],
        },
      },
    };
    console.log(entity);
    return entity;
  } catch (error: any | AxiosError) {
    return returnErrorEntity<ArborNumberFailResult>(error, {
      type: {
        type: Excel.CellValueType.string,
        basicValue: "ARBOR_NUMBER",
      },
      queryType: {
        type: Excel.CellValueType.string,
        basicValue: queryType,
      },
      query: {
        type: Excel.CellValueType.string,
        basicValue: query,
      },
      ticker: {
        type: Excel.CellValueType.string,
        basicValue: ticker,
      },
    });
  }
}

CustomFunctions.associate("ARBOR_NUMBER", answerNumber);