/* global CustomFunctions, AbortController */

import { getFromWorkbookSettings } from "../taskpane/taskpane";
import { cellProvider, defaultLongAnswerPrompt, defaultShortAnswerPrompt, successCellIcon } from "../constants";
import { arborAxiosLimiter } from "../http/limiter";
import { AxiosError } from "axios";
import { ArborDeepFailResult, ArborDeepSuccessResult } from "./types";
import { returnErrorEntity } from "./utils";

/**
 * Answers questions about a company based on its stock symbol in a short format.
 * @customfunction ARBOR_DEEP
 * @param {string} ticker The stock symbol of the company
 * @param {string} query The question to ask about the company
 * @param {boolean} [isLongAnswer] Whether to return a long answer
 * @param {CustomFunctions.CancelableInvocation} invocation Invocation object.
 * @returns {Promise<Excel.EntityCellValue>} The answer to the query
 * @cancellable
 */
export async function arborDeep(
  ticker: string,
  query: string,
  isLongAnswer: boolean,
  invocation: CustomFunctions.CancelableInvocation
): Promise<Excel.EntityCellValue> {
  const controller = new AbortController();

  invocation.onCanceled = () => {
    controller.abort();
  };

  if (!query.trim()) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Query is required");
  }
  if (!ticker.trim()) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Ticker is required");
  }

  try {
    const provider = await getFromWorkbookSettings("modelProvider");

    let queryPrompt = isLongAnswer ? defaultLongAnswerPrompt : defaultShortAnswerPrompt;
    queryPrompt = queryPrompt.replace(/\{\{query\}\}/g, query);
    queryPrompt = queryPrompt.replace(/\{\{ticker\}\}/g, ticker);

    const response = await arborAxiosLimiter.post(
      "/api/v1/answer",
      {
        query: queryPrompt,
        provider: provider || "openai",
      },
      {
        signal: controller.signal,
      }
    );

    const data = response.data;
    if (data.error) {
      throw new AxiosError(data.error, response.status.toString(), response.config, response.request);
    }

    const entity: ArborDeepSuccessResult = {
      type: Excel.CellValueType.entity,
      text: data.result.answer,
      provider: cellProvider,
      properties: {
        type: {
          type: Excel.CellValueType.string,
          basicValue: "ARBOR_DEEP",
        },
        success: {
          type: Excel.CellValueType.boolean,
          basicValue: true,
        },
        ticker: {
          type: Excel.CellValueType.string,
          basicValue: ticker,
        },
        query: {
          type: Excel.CellValueType.string,
          basicValue: query,
        },
        isLongAnswer: {
          type: Excel.CellValueType.boolean,
          basicValue: isLongAnswer || false,
        },
        answer: {
          type: Excel.CellValueType.string,
          basicValue: data.result.answer || "",
        },
        answerId: {
          type: Excel.CellValueType.string,
          basicValue: data.result.id || "",
        },
        url: {
          type: Excel.CellValueType.string,
          basicValue: `https://arborchat.ai/chat/${data.result.id}`,
        },
        answeredAt: {
          type: Excel.CellValueType.string,
          basicValue: response.config["metadata"].end.toISOString(),
        },
        queriedAt: {
          type: Excel.CellValueType.string,
          basicValue: response.config["metadata"].start.toISOString(),
        },
        ...(data.result.references?.length > 0
          ? {
              references: {
                type: Excel.CellValueType.array,
                elements: data.result.references.map((ref) => [ref.url as string, ref.title as string]),
              },
            }
          : {}),
      },
      layouts: {
        compact: {
          icon: successCellIcon,
        },
        card: {
          title: {
            property: "query",
          },
          sections: [
            {
              layout: "List",
              properties: ["answer", "url", ...(data.result.references?.length > 0 ? ["references"] : [])],
            },
            {
              title: "Metadata",
              layout: "Table",
              collapsible: true,
              collapsed: false,
              properties: ["ticker", "answerId", "isLongAnswer", "queriedAt", "answeredAt", "type", "success"],
            },
          ],
        },
      },
    };
    console.log(entity);
    return entity;
  } catch (error: any | AxiosError) {
    return returnErrorEntity<ArborDeepFailResult>(error, {
      type: {
        type: Excel.CellValueType.string,
        basicValue: "ARBOR_DEEP",
      },
      query: {
        type: Excel.CellValueType.string,
        basicValue: query || "",
      },
      ticker: {
        type: Excel.CellValueType.string,
        basicValue: ticker,
      },
      isLongAnswer: {
        type: Excel.CellValueType.boolean,
        basicValue: isLongAnswer || false,
      },
    });
  }
}

CustomFunctions.associate("ARBOR_DEEP", arborDeep);