/* global CustomFunctions, AbortController */

import { arborAxiosLimiter } from "../http/limiter";
import { AxiosError } from "axios";
import { ArborFastFailResult } from "./types";
import { returnErrorEntity } from "./utils";
import { cellProvider, successCellIcon } from "../constants";
import store from "../stores/store";
/**
 * Answers factual questions about a company based on its stock symbol.
 * @customfunction ARBOR_FAST
 * @param {string} ticker The stock symbol of the company
 * @param {string} query The factual question to ask about the company
 * @param {CustomFunctions.CancelableInvocation} invocation Invocation object.
 * @returns {Promise<Excel.EntityCellValue>} The factual answer to the query
 * @cancellable
 */
export async function arborFast(
  ticker: string,
  query: string,
  invocation: CustomFunctions.CancelableInvocation
): Promise<Excel.EntityCellValue> {
  const controller = new AbortController();

  invocation.onCanceled = () => {
    controller.abort();
  };

  if (!query.trim()) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Query is required");
  }
  if (!ticker.trim()) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Ticker is required");
  }

  try {
    const response = await arborAxiosLimiter.post(
      `/api/v1/screener/answer-factual`,
      {
        query: query,
        stockSymbol: ticker,
      },
      {
        signal: controller.signal,
      }
    );

    if (!response.data?.result) {
      throw new AxiosError("FailToAnswer", response.status.toString(), response.config, response.request);
    }

    const entity: Excel.EntityCellValue = {
      type: Excel.CellValueType.entity,
      text: `${response.data.result}`,
      provider: cellProvider,
      properties: {
        type: {
          type: Excel.CellValueType.string,
          basicValue: "ARBOR_FAST",
        },
        success: {
          type: Excel.CellValueType.boolean,
          basicValue: true,
        },
        ticker: {
          type: Excel.CellValueType.string,
          basicValue: ticker,
        },
        query: {
          type: Excel.CellValueType.string,
          basicValue: query || "",
        },
        answer: {
          type: Excel.CellValueType.string,
          basicValue: response.data.result,
        },
        answeredAt: {
          type: Excel.CellValueType.string,
          basicValue: response.config["metadata"].end.toISOString(),
        },
        queriedAt: {
          type: Excel.CellValueType.string,
          basicValue: response.config["metadata"].start.toISOString(),
        },
        ...(response.data.references?.length > 0
          ? {
              references: {
                type: Excel.CellValueType.array,
                elements: response.data.references.map((ref) => [ref.url as string, ref.title as string]),
              },
            }
          : {}),
      },
      layouts: {
        compact: {
          icon: successCellIcon,
        },
        card: {
          title: {
            property: "query",
          },
          subTitle: {
            property: "ticker",
          },
          sections: [
            {
              layout: "List",
              properties: ["answer", ...(response.data.references?.length > 0 ? ["references"] : [])],
            },
            {
              title: "Metadata",
              layout: "Table",
              collapsible: true,
              collapsed: false,
              properties: ["ticker", "query", "queriedAt", "answeredAt", "type", "success"],
            },
          ],
        },
      },
    };
    console.log(entity);
    return entity;
  } catch (error: any | AxiosError) {
    return returnErrorEntity<ArborFastFailResult>(error, {
      type: {
        type: Excel.CellValueType.string,
        basicValue: "ARBOR_FAST",
      },
      ticker: {
        type: Excel.CellValueType.string,
        basicValue: ticker,
      },
      query: {
        type: Excel.CellValueType.string,
        basicValue: query || "",
      },
    });
  }
}

CustomFunctions.associate("ARBOR_FAST", arborFast);