/* global localStorage, OfficeExtension */

export function setInLocalStorage(key: string, value: string) {
  const myPartitionKey = Office.context.partitionKey;

  // Check if local storage is partitioned.
  // If so, use the partition to ensure the data is only accessible by your add-in.
  if (myPartitionKey) {
    localStorage.setItem(myPartitionKey + key, value);
  } else {
    localStorage.setItem(key, value);
  }
}

export function getFromLocalStorage(key: string) {
  const myPartitionKey = Office.context.partitionKey;

  // Check if local storage is partitioned.
  if (myPartitionKey) {
    return localStorage.getItem(myPartitionKey + key);
  } else {
    return localStorage.getItem(key);
  }
}

export function clearLocalStorage() {
  localStorage.clear();
}

export async function getFromWorkbookSettings(key: string) {
  const result = await Excel.run(async (context) => {
    const settings = context.workbook.settings;
    const item = settings.getItemOrNullObject(key);
    await context.sync();
    if (item.isNullObject) {
      return null;
    }
    item.load("value");
    await context.sync();
    return item.value;
  });
  return result;
}

export async function setInWorkbookSettings(key: string, value: string) {
  await Excel.run(async (context) => {
    const settings = context.workbook.settings;
    const item = settings.getItemOrNullObject(key);
    await context.sync();
    if (item.isNullObject) {
      settings.add(key, value);
    } else {
      item.value = value;
    }
    await context.sync();
  });
}

export const ARBOR_CHAT_API = "https://api.arborchat.ai/stockchat";

Office.context.document.addHandlerAsync(Office.EventType.DocumentSelectionChanged, async function () {
  Excel.run(async function (context) {
    const sheet = context.workbook.worksheets.getActiveWorksheet();
    sheet.load("name");
    await context.sync();
    console.log('You are now on sheet "' + sheet.name + '"');
  }).catch(function (error) {
    console.log(error);
    if (error instanceof OfficeExtension.Error) {
      console.log("Debug info: " + JSON.stringify(error.debugInfo));
    }
  });
});
