import axios from "axios";
import { ARBOR_CHAT_API, getFromLocalStorage } from "../taskpane/taskpane";
import store from "../stores/store";
// eslint-disable-next-line no-undef
const pendingResolvers = [];
const MAX_REQUESTS_COUNT = 5;
let concurrentCount = 0;

const arborAxiosLimiter = axios.create({
  baseURL: ARBOR_CHAT_API,
  headers: {
    "Content-Type": "application/json",
  },
});
// Create interceptor to handle concurrent requests
arborAxiosLimiter.interceptors.request.use(function (config) {
  const apiKey = getFromLocalStorage("apiKey");
  const apiHost = getFromLocalStorage("apiHost") || ARBOR_CHAT_API;
  config.baseURL = apiHost;
  config.headers["X-API-KEY"] = apiKey;
  return new Promise((resolve) => {
    if (concurrentCount < MAX_REQUESTS_COUNT) {
      concurrentCount++;
      store.setState((prev) => ({
        requestRunningCount: prev?.requestRunningCount + 1,
        requestPendingCount: prev?.requestPendingCount,
      }));
      config["metadata"] = {
        start: new Date(),
      };
      resolve(config);
    } else {
      pendingResolvers.push(() => {
        concurrentCount++;
        config["metadata"] = {
          start: new Date(),
        };
        store.setState((prev) => ({
          requestRunningCount: prev?.requestRunningCount + 1,
          requestPendingCount: Math.max(0, prev?.requestPendingCount - 1),
        }));
        resolve(config);
      });
      store.setState((prev) => ({
        requestRunningCount: prev?.requestRunningCount,
        requestPendingCount: prev?.requestPendingCount + 1,
      }));
    }
  });
});

arborAxiosLimiter.interceptors.response.use(
  function (response) {
    return new Promise((resolve) => {
      const metadata = response.config["metadata"];
      if (metadata) {
        const duration = new Date().getTime() - metadata.start.getTime();
        console.log(`Request took ${duration}ms`);
        response.config["metadata"].end = new Date();
      }
      concurrentCount = Math.max(0, concurrentCount - 1);
      if (pendingResolvers.length > 0) {
        pendingResolvers.shift()();
      }
      store.setState((prev) => ({
        requestRunningCount: Math.max(0, prev?.requestRunningCount - 1),
        requestPendingCount: Math.max(0, prev?.requestPendingCount - 1),
      }));
      resolve(response);
    });
  },
  function (error) {
    const metadata = error.config["metadata"];
    if (metadata) {
      const duration = new Date().getTime() - metadata.start.getTime();
      console.log(`Request took ${duration}ms`);
      error.config["metadata"].end = new Date();
    }
    concurrentCount = Math.max(0, concurrentCount - 1);
    if (pendingResolvers.length > 0) {
      pendingResolvers.shift()();
    }
    store.setState((prev) => ({
      requestRunningCount: Math.max(0, prev?.requestRunningCount - 1),
      requestPendingCount: Math.max(0, prev?.requestPendingCount - 1),
    }));
    return Promise.reject(error);
  }
);

export { arborAxiosLimiter };
